import React from 'react';

export const formatBoldItalicText = (text) => {
   // Look for patterns to apply styles dynamically
   const parts = text.split(/(\*\*.*?\*\*|__.*?__|\*\*__.*?__\*\*|__\*\*.*?\*\*__)/);

   return parts.map((part, index) => {

      if (part.startsWith('**__') && part.endsWith('__**')) {
         // Bold and italic text
         const content = part.substring(4, part.length - 4);
         return <strong key={index}><em>{content}</em></strong>;
      } else if (part.startsWith('__**') && part.endsWith('**__')) {
         // Bold and italic text (alternate order)
         const content = part.substring(4, part.length - 4);
         return <em key={index}><strong>{content}</strong></em>;
      } else if (part.startsWith('**') && part.endsWith('**')) {
         // Bold text
         const content = part.substring(2, part.length - 2);
         return <strong key={index}>{content}</strong>;
      } else if (part.startsWith('__') && part.endsWith('__')) {
         // Italic text
         const content = part.substring(2, part.length - 2);
         return <em key={index}>{content}</em>;
      } else {
         return part;
      }
   });
};


