import React, { Component } from "react";
import Header from "../../components/header/Header";
import Footer from "../../components/footer/Footer";
import TopButton from "../../components/topButton/TopButton";
import ExperienceAccordion from "../../containers/experienceAccordion/ExperienceAccordion.js";
import "./Experience.css";
import { Fade } from "react-awesome-reveal";
import ExperienceImg from "./ExperienceImg";
import { getAnalytics, logEvent } from 'firebase/analytics'
import { connect } from "react-redux";


class Experience extends Component {
  render() {
	const {experience} = this.props.portfolio

    const analytics = getAnalytics()

	logEvent(analytics, 'experience_page', {
		content_type: 'screen',
		content_id: 'P1002',
		items: [{ name: 'Kittens' }]
	})
    // let con1 = Portfolio.getInstance('mysqldb1');
    // let con2 = Portfolio.getInstance('mysqldb2');

    //the connections are the same
    // console.log("con1: "+con1.conString);
    // console.log("con2: "+con2.conString);

    const theme = this.props.theme;
    return (
      <div className="experience-main">
        <Header theme={theme} />
        <div className="basic-experience">
          <Fade direction="bottom" duration={2000} distance="40px">
            <div className="experience-heading-div">
              <div className="experience-heading-img-div">
                {/* <img
									src={require(`../../assests/images/${experience["header_image_path"]}`)}
									alt=""
								/> */}
                <ExperienceImg theme={theme} />
              </div>
              <div className="experience-heading-text-div">
                <h1
                  className="experience-heading-text"
                  style={{ color: theme.text }}
                >
                  {experience.title}
                </h1>
                <h3
                  className="experience-heading-sub-text"
                  style={{ color: theme.text }}
                >
                  {experience["subtitle"]}
                </h3>
                <p
                  className="experience-header-detail-text subTitle"
                  style={{ color: theme.secondaryText }}
                >
                  {experience["description"]}
                </p>
              </div>
            </div>
          </Fade>
        </div>
        <ExperienceAccordion sections={experience["sections"]} theme={theme} />
        <Footer theme={this.props.theme} onToggle={this.props.onToggle} />
        <TopButton theme={this.props.theme} />
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    portfolio: state.portfolio
  };
};
export default connect(mapStateToProps) (Experience);
